import axios from "axios";
import { useRef } from "react";
import "./register.css";
import {useHistory} from "react-router-dom";

function Register() {
  const username = useRef();
  const email = useRef();
  const password = useRef();
  const confirmpassword = useRef();
  const history = useHistory();

  const handleClick = async (e) =>{
    e.preventDefault();
    if(confirmpassword.current.value !== password.current.value){
      confirmpassword.current.setCustomValidity("Passwords don't match!");
    }else{
      const user ={
        username : username.current.value,
        email : email.current.value,
        password : password.current.value
      };
      try{
       await axios.post("/auth/register",user);
       history.push("/login");
      }
      catch(err){
        console.log(err);
      }
    }
  }

  return (
    <div className="login">
      <div className="loginWrapper">
        <div className="loginLeft">
          <div className="loginLogo">Zocially</div>
          <span className="loginDesc">
            Connect with Friends through Zocially
          </span>
        </div>
        <div className="loginRight">
          <form className="loginBox" onSubmit={handleClick}>
            <input placeholder="Username" type="text" ref={username} required className="loginInput" />
            <input placeholder="Email" type="email" ref={email} required className="loginInput" />
            <input placeholder="Password"type="password" ref={password} required className="loginInput" minLength="6" />
            <input placeholder="Confirm Password" type="password" ref={confirmpassword} required className="loginInput" />
            <button className="loginButton"type="submit">Sign Up</button>            
            <button className="loginRegisterButton">Log into Account</button>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Register;
