import "./login.css";
import {useContext, useRef} from "react";
import { loginCall } from "../../apiCalls";
import { AuthContext } from "../../context/AuthContext";
import {CircularProgress} from "@material-ui/core";

function Login() {

  const email = useRef();
  const password = useRef();
  const{user,isFetching, error, dispatch} = useContext(AuthContext);

  const handleClick = (e) =>{
     e.preventDefault();
     loginCall({email:email.current.value,
      password:password.current.value},dispatch)
  };

  console.log(user);
  return (
    <div className="login">
      <div className="loginWrapper">
        <div className="loginLeft">
          <div className="loginLogo">Zocially</div>
          <span className="loginDesc">
            Connect with Friends through Zocially
          </span>
        </div>
        <div className="loginRight">
          <form className="loginBox" onSubmit={handleClick}>
            <input placeholder="Email" required type="email" className="loginInput" ref={email}/>
            <input placeholder="Password"type="password" minLength="6" required className="loginInput" ref={password}/>
            <button className="loginButton"type="submit" disabled={isFetching}>{isFetching ?<CircularProgress color="inherit" size="20px" />: "Log In"}</button>
            <span className="loginForgot">Forgot Password?</span>
            <button className="loginRegisterButton">{isFetching ? <CircularProgress color="inherit" size="20px" /> :"Create New Account"}</button>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Login;
